/* 全体 */
div.sim_frame {
    height: calc(100svh - 60px);
    display: flex;
    justify-content: center;
    /* 水平方向中央 */
    margin: auto;
    overflow-y: hidden;
}

.adjust_width {
    width: 450px;
}

@media screen and (max-width: 768px) {
    div.sim_frame {
        display: block;
        overflow-y: scroll;
    }
}

/* 部隊/ステータス */
input.troops_btn {
    width: 40px;
    height: 40px;
    font-size: 25px;
    color: #fff;
    border-radius: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 5px;
    box-shadow: 0 6px 0 0 #005400;
    transform: translateY(-6px);
}

input.troops_btn:hover {
    box-shadow: 0 2px 0 0 #005400;
    transform: translateY(-2px);
}

input.troops_btn.selected_troops {
    box-shadow: 0 2px 0 0 #005400;
    transform: translateY(-2px);
}

#style_reset_btn {
    width: 50px;
    font-size: 12px;
}

/* ステータスエリア */
.unit_setting_area {
    width: 450px;
    font-size: 0.875rem;
}

#char_list {
    width: 482px;
}

.dragging {
    opacity: 0.5;
    /* ドラッグ中は半透明に */
}

.limit:disabled {
    border: solid 1px black;
}

#enemy_select_count {
    border: none;
    opacity: 1;
}

.area_title {
    font-weight: bold;
    margin-left: 5px;
    font-size: 14px;
}

@media screen and (max-width: 482px) {
    .unit_setting_area {
        margin: auto;
        width: 400px;
    }

    #char_list {
        width: 400px;
    }

    .adjust_width {
        width: 100%;
        max-width: 400px;
        min-width: 375px;
    }

    .limit,
    .earring,
    .bracelet,
    .chain,
    .init_sp,
    .passive {
        width: 50px;
    }
}

/* 戦闘開始 */
.battle_start {
    font-size: 16px;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.1s ease;
}

.battle_setting {
    font-size: 16px;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.1s ease;
}

/* 敵情報 */
.surround_area {
    border: solid gray 2px;
    border-radius: 20px;
    padding: 5px;

}

#enemy_select {
    margin-left: 1rem;
    font-size: 14px;
}

#enemy_class {
    max-width: 120px;
}

#enemy_list {
    max-width: 225px;
}

.enemy_weak {
    font-weight: bold;
    color: red;
}

.enemy_resist {
    font-weight: bold;
    color: blue;
}

.enemy_type_icon {
    width: 32px;
    height: 32px;
    margin-top: 4px;
    margin-left: 5px;
    margin-bottom: -8px;
}

.enemy_type_value {
    text-align: center;
    width: 32px;
    background-color: rgba(0, 0, 0, 0);
    margin-left: 5px;
}

@media screen and (max-width: 482px) {
    #enemy_class {
        max-width: 100px;
    }

    #enemy_list {
        max-width: 200px;
    }

    #enemy_select {
        margin-left: 0px;
    }
}

/* 補助入力 */
.step_od_gauge {
    text-align: right;
    border: #000 solid 1px;
    height: 25px;
}

.step_sp {
    width: 40px;
    text-align: right;
    border: #000 solid 1px;
    height: 25px;
}

/* バトルエリア */
#battle_area {
    height: 97%;
    overflow-y: auto;
    margin-left: 5px;
    margin-right: 5px;
}

#battle_display {
    background-color: #fff;
}

.show_mode {
    width: 1280px;
}

.hide_mode {
    width: 1590px;
}

#btnDownload {
    font-size: 14px;
}

.turn_button {
    width: 60px;
    font-size: 14px;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.1s ease;
}

.trigger_over_drive {
    display: flex !important;
    font-size: 14px;
    border: none;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.1s ease;
    appearance: none;
    background-color: #e2e2e2;
    border-radius: 100vh;
    padding-right: 4px;
    padding-left: 4px;
}

.trigger_over_drive:checked {
    background-color: #149f14;
    color: white;
    transform: scale(1.05);
}

.trigger_over_drive:before {
    content: 'OD発動';
}

.turn {
    border: solid gray 2px;
    border-radius: 20px;
    margin: 0px 0px 3px 0px;
    padding: 0px 5px 0px 5px;
    display: flex;
}

.party_member {
    display: flex;
}

.remaek_text {
    margin: 5px 5px 0px 5px;
    border: solid 1px;
    width: 240px;
    height: 60px;
    resize: none;
}

@media screen and (max-width: 1400px) {
    .show_mode {
        width: 400px;
    }

    .hide_mode {
        width: 400px;
    }

    .turn {
        display: block;
    }

    .party_member {
        display: block;
    }

    .remaek_text {
        width: 375px;
        height: 25px;
    }

    .turn_button {
        width: 360px;
        width: 80px;
    }
}

@media screen and (max-width: 768px) {

    .show_mode,
    .hide_mode {
        margin: auto;
    }
}

.turn_header_area {
    width: 100%;
}

.turn_header_top {
    display: flex;
    justify-content: space-between;
}

.turn_number {
    font-weight: bold;
    margin-bottom: -3px;
}

.left {
    flex: 1;
}

.right {
    flex: 1;
}

.enemy_icon {
    width: 23px;
    height: 23px;
}

.enemy_count {
    width: 50px;
}

.inc_od_icon {
    width: 61px;
    margin-left: -3px;
}

.od_icon {
    width: 61px;
    height: 48px;
}

.od_number {
    width: 48px;
    height: 48px;
    margin-left: 11px;
}

.od_text {
    padding-top: 5px;
    font-size: 13px;
}

.od_minus {
    font-weight: bold;
    color: red;
}

.unit_selected {
    background-color: #FF0;
}

.show_mode .front_area .unit_skill {
    width: 125px;
}

.show_mode .back_area .unit_skill {
    width: 72px;
}

.hide_mode .unit_skill {
    width: 150px;
}

.show_mode .icon_list {
    width: 72px;
    overflow: hidden;
}

.hide_mode .icon_list {
    width: 96px;
    overflow: hidden;
}

@media screen and (max-width: 1400px) {
    .hide_mode .front_area .unit_skill {
        width: 125px;
    }

    .hide_mode .back_area .unit_skill {
        width: 72px;
    }

    .hide_mode .icon_list {
        width: 72px;
        overflow: hidden;
    }

    .back_area .icon_list {
        display: none;
    }
}

.unit_skill {
    font-size: 12px;
}

.action_select {
    margin-top: 10px;
    font-size: 12px;
    width: 120px;
}

.unit_sp {
    font-size: 24px;
    font-weight: bold;
    color: #fff;
    text-shadow: 2px 2px 10px #000,
        -2px 2px 10px #000,
        2px -2px 10px #000,
        -2px -2px 10px #000;
    margin-top: -56px;
    text-align: right;
    pointer-events: none;
    white-space: nowrap;
}

.unit_sp.minus {
    text-shadow: 2px 2px 10px #f00,
        -2px 2px 10px #f00,
        2px -2px 10px #f00,
        -2px -2px 10px #f00;
}


.unit_ep {
    font-size: 16px;
    font-weight: bold;
    color: #ff7f00;
    text-shadow: 2px 2px 10px #000,
        -2px 2px 10px #000,
        2px -2px 10px #000,
        -2px -2px 10px #000;
    text-align: right;
    pointer-events: none;
}

.front_area,
.back_area {
    height: 72px;
}

.back_area .unit_sp {
    margin-right: 8px;
}

img.unit_style {
    width: 48px;
    height: 48px;
}

img.unit_buff {
    width: 24px;
    height: 24px;
}

.enemy_count {
    line-height: 21px;
    font-size: 14px;
}

.enemy_icon_list {
    width: 290px;
    margin-left: 5px;
    margin-top: -1px;
}

.hidden-option {
    display: none;
}

/* アイコンスクロール */
.scroll-container {
    overflow: hidden;
    white-space: nowrap;
    display: flex;
}

.scroll-content {
    display: flex;
}


@media screen and (max-width: 768px) {
    .action_select {
        width: 145px;
    }
}

/* スキル画面 */
.skill_select_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1px;
}

/* モーダル画面*/
.modal_label {
    font-size: 20px;
    font-weight: bold;
}

/* 閉じるボタンのスタイル */
.modal-close {
    position: absolute;
    top: 5px;
    right: 15px;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: #333;
    transition: transform 0.2s, color 0.2s;
}

.select_effect {
    display: block;
    text-align: center;
}

.effect_button {
    width: 150px;
    font-size: 16px;
    margin: 2px;
}

.icon_buff_detail {
    width: 40px;
    height: 40px;
}

.detail_line_height {
    line-height: 1.2;
}

/* save&load */
#btnSave,
#btnLoad {
    font-size: 14px;
    width: 60px;
    margin-left: 2px;
    margin-right: 2px;
}

ul.save_load {
    list-style-type: none;
    padding: 0;
}

ul.save_load li {
    width: 100%;
    padding: 8px;
    margin: 3px 0;
    border: 1px solid #ccc;
    background: #f9f9f9;
    cursor: pointer;
    user-select: none;
}